.card {
    background: #fff;
    border-radius: 2px;
    margin: 1rem;
    height: 80%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:white;
  }
  .card > .img-container > img {
    width: 60%;
    height: 100%;
  }